<template>
  <div class="app common-layout">
    <el-container>
      <el-header class="header" v-if="screenWidth > 768">
        <Header @toggle-sidebar="toggleCollapse"/>
      </el-header>
      
      <el-header class="header_mobile" v-else>
        <HeaderMobile @toggle-sidebar="toggleCollapse"/>
      </el-header>
      <el-container>
        <Aside v-if="screenWidth > 768" v-model:isCollapse="isAsideCollapsed"/>
        
        <el-main class="main_hight">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Header from './components/baseComponents/Header.vue'
import Aside from './components/baseComponents/Aside.vue'
import HeaderMobile from './components/baseComponents/HeaderMobile.vue'

import { ref, onMounted, onBeforeUnmount } from 'vue';


export default {
  name: 'App',
  components: {
    Header,
    Aside,
    HeaderMobile,
  },
  data() {
    return {
      isAsideCollapsed: true
    }
  },
  setup() {
    const screenWidth = ref(window.innerWidth);

    onMounted(() => {
      window.addEventListener('resize', updateScreenWidth);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('resize', updateScreenWidth);
    });

    const updateScreenWidth = () => {
      screenWidth.value = window.innerWidth;
    };

    return {
      screenWidth
    };
  },
  methods: {
    toggleCollapse() {
      window.requestAnimationFrame(() => {
        this.isAsideCollapsed = !this.isAsideCollapsed;
      });
    }
  },
}
</script>

<style>
  * {
    font-family: 'Ubuntu', sans-serif;
  }
  .header, .header_mobile{
    display: flex;
    align-items: center;
    min-height: 80px;
    border-bottom: 1px solid #EEF0F3;
  }
  .header_mobile{
    min-height: 60px;
  }
  .main_hight{
    height: calc(100vh - 96px);
  }
</style>
